<template>
  <transition name="slide">
    <div class="app" :style="{minHeight: height + 'px'}">
      <div id="myCursor" :style="{left: clientX + 'px', top: clientY + 'px'}" class="myCursor"></div>
      <div>
          <router-view></router-view>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  data () {
    return {
      height: '',
      clientX: 0,
      clientY: 0
    }
  },
  mounted () {
    var screabHeight = document.documentElement.clientHeight
    this.height = screabHeight
    let _this = this
    document.onmousemove = function (e) {
      _this.clientX = e.clientX
      _this.clientY = e.clientY
    }
  }
}
</script>

<style scoped>
.app {
  background-color: rgb(62, 22, 36);
}
</style>


